import React from "react";
import { Grid } from "@mui/material";
import { styled } from "@mui/system";

const CountDownCard = styled("div")(({ theme }) => ({
    padding: 20,
    textAlign: "center",
    borderRadius: 5,
    minWidth: 70,
    maxWidth: 70,
    background: 'linear-gradient(265.56deg, #fad759 -0.27%, #fad759 -0.26%, #dba72f 50.19%, #cf952a 98.59%)',
    margin: "auto",
    color:'black',
    textShadow: 'rgba(147, 122, 122, 0.87) 2px 2px'
}));

export default function Timer({ days, hours, minutes, seconds, completed }) {
    return (
        <Grid container spacing={1} justifyContent="center">
            <Grid item xs={12} sm={3}>
                <CountDownCard>{days}</CountDownCard>
                <p style={{textAlign:'center'}}>Days</p>
            </Grid>
            <Grid item xs={12} sm={3}>
                <CountDownCard>{hours}</CountDownCard>
                <p style={{textAlign:'center'}}>Hours</p>
            </Grid>
            <Grid item xs={12} sm={3}>
                <CountDownCard>{minutes}</CountDownCard>
                <p style={{textAlign:'center'}}>Minutes</p>
            </Grid>
            <Grid item xs={12} sm={3}>
                <CountDownCard>{seconds}</CountDownCard>
                <p style={{textAlign:'center'}}>Second</p>
            </Grid>
        </Grid>
    );
}
